@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
:root{--calc-res-color: #2f2d2f;--calc-bg: #fff}.dark{--calc-res-color: #fff;--calc-bg: #373737}*{padding:0;margin:0;box-sizing:border-box;-webkit-tap-highlight-color:transparent}body{font-family:"Montserrat",sans-serif;font-weight:400;line-height:1.5;height:100vh;background-color:#f5f5f5;color:#6d6d6d;display:flex;justify-content:center;align-items:center}@media only screen and (max-width: 800px){body{display:unset}}button{font-family:"Montserrat",sans-serif;cursor:pointer}.calculator{padding:20px;height:700px;width:400px;overflow:hidden;border-radius:20px;background-color:#fff;background-color:var(--calc-bg);box-shadow:rgba(100,100,111,.2) 0px 7px 29px 0px;display:flex;flex-direction:column;justify-content:flex-end}@media only screen and (max-width: 800px){.calculator{width:100%;height:100vh;border-radius:0}}.calculator__btns{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:10px;gap:10px}.calculator__btns button{border:0;outline:0;border-radius:10px;font-size:1.75rem;font-weight:600;background-color:transparent;color:#6d6d6d}.calculator__btns button:nth-child(17){font-weight:normal}.calculator__btns button:last-child{border-radius:50%;color:#fff;background-image:linear-gradient(312deg, #cd6e36, #d400ff)}.calculator__btns .btn__op{color:#d400ff}.calculator__result{position:relative;margin-bottom:20px;text-align:center;display:flex;flex-direction:column;align-items:flex-end;justify-content:flex-end}.calculator__result__exp{font-size:3rem;line-height:1;transform-origin:right bottom;transition:transform .3s ease;color:#2f2d2f;color:var(--calc-res-color)}.calculator__result__exp:last-child{position:absolute;right:0}.calculator__result__exp span{display:inline-block;overflow:hidden;transition:width .3s ease}
